body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.search-container {
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.select-container {
  flex-grow: 1;
  margin-right: 10px;
}

.loader-container {
  text-align: center;
  margin-bottom: 20px;
  display: none;
}

.loader {
  width: 30px;
  height: 30px;
  border: 4px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 0 auto;
  animation: spin 1s linear infinite;
}

.loader-text {
  color: #777;
  margin-top: 10px;
  font-size: 14px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  margin-bottom: 20px;
  display: none;
}

.cat-info {
  text-align: center;
  margin-bottom: 20px;
  display: none;
}

.cat-info img {
  width: 100%;
  max-width: 300px;
  height: auto;
  margin-bottom: 10px;
}

.cat-info h2, .cat-info p {
  margin: 0;
  font-weight: bold;
}

